import React, { useState, useEffect } from 'react';
import { useParams, Link, useNavigate } from 'react-router-dom';
import { Container, Card, Row, Col, Table, Badge, Button, Spinner } from 'react-bootstrap';
import { getOrderById, cancelOrder } from '../services/orderService';

const orderStatusVariants = {
  'pending': 'warning',
  'processing': 'info',
  'shipped': 'primary',
  'delivered': 'success',
  'cancelled': 'danger',
  'paid': 'success',
  'failed': 'danger'
};

const orderStatusLabels = {
  'pending': 'Chờ xác nhận',
  'processing': 'Đang xử lý',
  'shipped': 'Đang giao hàng',
  'delivered': 'Đã giao hàng',
  'cancelled': 'Đã hủy',
  'paid': 'Đã thanh toán',
  'failed': 'Thanh toán thất bại'
};

const paymentMethods = {
  'cod': 'Thanh toán khi nhận hàng (COD)',
  'banking': 'Chuyển khoản ngân hàng',
  'momo': 'Ví MoMo',
  'vnpay': 'VNPay',
  'zalopay': 'ZaloPay'
};

const OrderDetail = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  
  const [order, setOrder] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [cancellingOrder, setCancellingOrder] = useState(false);
  
  // Hàm format giá tiền
  const formatPrice = (price) => {
    return new Intl.NumberFormat('vi-VN', { style: 'currency', currency: 'VND' }).format(price);
  };
  
  // Hàm format ngày
  const formatDate = (dateString) => {
    if (!dateString) return 'N/A';
    const date = new Date(dateString);
    return new Intl.DateTimeFormat('vi-VN', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit'
    }).format(date);
  };
  
  // Lấy thông tin chi tiết đơn hàng
  useEffect(() => {
    const fetchOrderDetail = async () => {
      try {
        setLoading(true);
        setError(null);
        
        const response = await getOrderById(id);
        
        if (response.success) {
          setOrder(response.order);
        } else {
          setError(response.message || 'Không thể tải thông tin đơn hàng');
        }
      } catch (err) {
        setError('Lỗi kết nối: ' + err.message);
      } finally {
        setLoading(false);
      }
    };
    
    fetchOrderDetail();
  }, [id]);
  
  // Hàm xử lý hủy đơn hàng
  const handleCancelOrder = async () => {
    if (!window.confirm('Bạn có chắc chắn muốn hủy đơn hàng này?')) {
      return;
    }
    
    try {
      setCancellingOrder(true);
      const result = await cancelOrder(id);
      
      if (result.success) {
        setOrder({ ...order, status: 'cancelled' });
        alert(result.message);
      } else {
        alert(result.message || 'Không thể hủy đơn hàng');
      }
    } catch (err) {
      alert('Lỗi khi hủy đơn hàng: ' + err.message);
    } finally {
      setCancellingOrder(false);
    }
  };
  
  if (loading) {
    return (
      <Container className="my-5 text-center">
        <Spinner animation="border" role="status">
          <span className="visually-hidden">Đang tải...</span>
        </Spinner>
        <p className="mt-3">Đang tải thông tin đơn hàng...</p>
      </Container>
    );
  }
  
  if (error) {
    return (
      <Container className="my-5">
        <div className="alert alert-danger" role="alert">
          {error}
        </div>
        <div className="text-center mt-3">
          <Button variant="primary" onClick={() => navigate('/my-orders')}>
            Quay lại danh sách đơn hàng
          </Button>
        </div>
      </Container>
    );
  }
  
  if (!order) {
    return (
      <Container className="my-5 text-center">
        <h3>Không tìm thấy đơn hàng</h3>
        <p>Đơn hàng không tồn tại hoặc bạn không có quyền xem.</p>
        <Button variant="primary" onClick={() => navigate('/my-orders')}>
          Quay lại danh sách đơn hàng
        </Button>
      </Container>
    );
  }
  
  return (
    <Container className="my-4">
      <div className="mb-4">
        <Button variant="secondary" onClick={() => navigate('/my-orders')}>
          &larr; Quay lại danh sách đơn hàng
        </Button>
      </div>
      
      <h2 className="mb-4">Chi Tiết Đơn Hàng #{order._id}</h2>
      
      <Row className="mb-4">
        <Col md={6}>
          <Card className="mb-3">
            <Card.Header as="h5">Thông Tin Đơn Hàng</Card.Header>
            <Card.Body>
              <Row className="mb-2">
                <Col md={5} className="fw-bold">Mã đơn hàng:</Col>
                <Col md={7}>{order._id}</Col>
              </Row>
              <Row className="mb-2">
                <Col md={5} className="fw-bold">Ngày đặt hàng:</Col>
                <Col md={7}>{formatDate(order.createdAt)}</Col>
              </Row>
              <Row className="mb-2">
                <Col md={5} className="fw-bold">Trạng thái:</Col>
                <Col md={7}>
                  <Badge bg={orderStatusVariants[order.status] || 'secondary'}>
                    {orderStatusLabels[order.status] || order.status}
                  </Badge>
                </Col>
              </Row>
              <Row className="mb-2">
                <Col md={5} className="fw-bold">Tổng tiền:</Col>
                <Col md={7} className="fw-bold text-danger">
                  {formatPrice(order.totalAmount)}
                </Col>
              </Row>
              <Row className="mb-2">
                <Col md={5} className="fw-bold">Phương thức thanh toán:</Col>
                <Col md={7}>
                  {paymentMethods[order.paymentMethod] || order.paymentMethod}
                </Col>
              </Row>
              
              {/* Hiển thị thông tin thanh toán nếu có */}
              {order.paymentDetails && (
                <>
                  {order.paymentDetails.transactionId && (
                    <Row className="mb-2">
                      <Col md={5} className="fw-bold">Mã giao dịch:</Col>
                      <Col md={7}>{order.paymentDetails.transactionId}</Col>
                    </Row>
                  )}
                  {order.paymentDetails.paidAt && (
                    <Row className="mb-2">
                      <Col md={5} className="fw-bold">Thời gian thanh toán:</Col>
                      <Col md={7}>{formatDate(order.paymentDetails.paidAt)}</Col>
                    </Row>
                  )}
                </>
              )}
            </Card.Body>
          </Card>
          
          {/* Nút hủy đơn hàng */}
          {(order.status === 'pending' || order.status === 'processing') && (
            <div className="mt-3">
              <Button
                variant="danger"
                onClick={handleCancelOrder}
                disabled={cancellingOrder}
              >
                {cancellingOrder ? (
                  <>
                    <Spinner
                      as="span"
                      animation="border"
                      size="sm"
                      role="status"
                      aria-hidden="true"
                      className="me-2"
                    />
                    Đang hủy đơn hàng...
                  </>
                ) : (
                  'Hủy đơn hàng'
                )}
              </Button>
            </div>
          )}
        </Col>
        
        <Col md={6}>
          <Card>
            <Card.Header as="h5">Thông Tin Khách Hàng</Card.Header>
            <Card.Body>
              <Row className="mb-2">
                <Col md={4} className="fw-bold">Họ tên:</Col>
                <Col md={8}>{order.name}</Col>
              </Row>
              <Row className="mb-2">
                <Col md={4} className="fw-bold">Email:</Col>
                <Col md={8}>{order.email}</Col>
              </Row>
              <Row className="mb-2">
                <Col md={4} className="fw-bold">Số điện thoại:</Col>
                <Col md={8}>{order.phone}</Col>
              </Row>
              <Row className="mb-2">
                <Col md={4} className="fw-bold">Địa chỉ:</Col>
                <Col md={8}>{order.address}</Col>
              </Row>
              {order.city && (
                <Row className="mb-2">
                  <Col md={4} className="fw-bold">Thành phố:</Col>
                  <Col md={8}>{order.city}</Col>
                </Row>
              )}
              {order.note && (
                <Row className="mb-2">
                  <Col md={4} className="fw-bold">Ghi chú:</Col>
                  <Col md={8}>{order.note}</Col>
                </Row>
              )}
            </Card.Body>
          </Card>
        </Col>
      </Row>
      
      <Card className="mb-4">
        <Card.Header as="h5">Sản Phẩm Đã Đặt</Card.Header>
        <Card.Body>
          <Table responsive striped bordered>
            <thead>
              <tr>
                <th style={{ width: '80px' }}>STT</th>
                <th style={{ width: '100px' }}>Hình ảnh</th>
                <th>Sản phẩm</th>
                <th style={{ width: '100px' }}>Đơn giá</th>
                <th style={{ width: '100px' }}>Số lượng</th>
                <th style={{ width: '150px' }}>Thành tiền</th>
              </tr>
            </thead>
            <tbody>
              {order.products && order.products.map((item, index) => (
                <tr key={index}>
                  <td className="text-center">{index + 1}</td>
                  <td>
                    {item.productId && item.productId.image ? (
                      <img 
                        src={item.productId.image} 
                        alt={item.productId.name} 
                        style={{ width: '80px', height: '80px', objectFit: 'cover' }}
                      />
                    ) : (
                      <div 
                        className="bg-light d-flex align-items-center justify-content-center" 
                        style={{ width: '80px', height: '80px' }}
                      >
                        No image
                      </div>
                    )}
                  </td>
                  <td>
                    {item.productId ? (
                      <Link to={`/product/${item.productId._id}`}>
                        {item.productId.name}
                      </Link>
                    ) : (
                      'Sản phẩm không còn tồn tại'
                    )}
                  </td>
                  <td className="text-end">
                    {item.productId && formatPrice(item.productId.price)}
                  </td>
                  <td className="text-center">{item.quantity}</td>
                  <td className="text-end">
                    {item.productId && formatPrice(item.productId.price * item.quantity)}
                  </td>
                </tr>
              ))}
              <tr>
                <td colSpan="5" className="text-end fw-bold">Tổng cộng:</td>
                <td className="text-end fw-bold text-danger">
                  {formatPrice(order.totalAmount)}
                </td>
              </tr>
            </tbody>
          </Table>
        </Card.Body>
      </Card>
    </Container>
  );
};

export default OrderDetail; 