import React, { useState, useEffect, useCallback } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { Row, Col, Table, Badge, Button, Spinner, Pagination, Form } from 'react-bootstrap';
import { getMyOrders, cancelOrder } from '../services/orderService';

const orderStatusVariants = {
  'pending': 'warning',
  'processing': 'info',
  'shipped': 'primary',
  'delivered': 'success',
  'cancelled': 'danger',
  'paid': 'success',
  'failed': 'danger'
};

const orderStatusLabels = {
  'pending': 'Chờ xác nhận',
  'processing': 'Đang xử lý',
  'shipped': 'Đang giao hàng',
  'delivered': 'Đã giao hàng',
  'cancelled': 'Đã hủy',
  'paid': 'Đã thanh toán',
  'failed': 'Thanh toán thất bại'
};

const MyOrders = () => {
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [filterStatus, setFilterStatus] = useState('');
  const [cancellingOrderId, setCancellingOrderId] = useState(null);
  
  const navigate = useNavigate();
  
  // Hàm format giá tiền
  const formatPrice = (price) => {
    return new Intl.NumberFormat('vi-VN', { style: 'currency', currency: 'VND' }).format(price);
  };
  
  // Hàm format ngày
  const formatDate = (dateString) => {
    const date = new Date(dateString);
    return new Intl.DateTimeFormat('vi-VN', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit'
    }).format(date);
  };
  
  // Lấy danh sách đơn hàng (sử dụng useCallback)
  const fetchOrders = useCallback(async () => {
    setLoading(true);
    setError(null);
    
    try {
      const options = {
        page: currentPage,
        limit: 10
      };
      
      if (filterStatus) {
        options.status = filterStatus;
      }
      
      const response = await getMyOrders(options);
      
      if (response.success) {
        setOrders(response.orders);
        setTotalPages(response.pagination.totalPages);
      } else {
        setError(response.message || 'Không thể tải danh sách đơn hàng');
      }
    } catch (err) {
      setError('Lỗi kết nối: ' + err.message);
    } finally {
      setLoading(false);
    }
  }, [currentPage, filterStatus]);
  
  // Hook để tải dữ liệu khi trang được render hoặc khi các bộ lọc thay đổi
  useEffect(() => {
    fetchOrders();
  }, [fetchOrders]);
  
  // Hàm xử lý chuyển trang
  const handlePageChange = (page) => {
    setCurrentPage(page);
  };
  
  // Hàm xử lý thay đổi bộ lọc trạng thái
  const handleStatusFilterChange = (e) => {
    setFilterStatus(e.target.value);
    setCurrentPage(1); // Reset về trang đầu tiên khi thay đổi bộ lọc
  };
  
  // Hàm xử lý xem chi tiết đơn hàng
  const handleViewOrderDetail = (orderId) => {
    navigate(`/my-orders/${orderId}`);
  };
  
  // Hàm xử lý hủy đơn hàng
  const handleCancelOrder = async (orderId) => {
    try {
      setCancellingOrderId(orderId);
      const result = await cancelOrder(orderId);
      
      if (result.success) {
        // Cập nhật lại danh sách đơn hàng
        setOrders(orders.map(order => 
          order._id === orderId ? { ...order, status: 'cancelled' } : order
        ));
        alert(result.message);
      } else {
        alert(result.message || 'Không thể hủy đơn hàng');
      }
    } catch (err) {
      alert('Lỗi khi hủy đơn hàng: ' + err.message);
    } finally {
      setCancellingOrderId(null);
    }
  };
  
  // Render phân trang
  const renderPagination = () => {
    if (totalPages <= 1) return null;
    
    const items = [];
    
    // Nút Previous
    items.push(
      <Pagination.Prev 
        key="prev"
        onClick={() => handlePageChange(Math.max(1, currentPage - 1))}
        disabled={currentPage === 1}
      />
    );
    
    // Nút trang đầu
    if (currentPage > 2) {
      items.push(
        <Pagination.Item key={1} onClick={() => handlePageChange(1)}>
          1
        </Pagination.Item>
      );
      
      if (currentPage > 3) {
        items.push(<Pagination.Ellipsis key="ellipsis-start" />);
      }
    }
    
    // Các trang ở giữa
    for (
      let page = Math.max(1, currentPage - 1);
      page <= Math.min(totalPages, currentPage + 1);
      page++
    ) {
      items.push(
        <Pagination.Item
          key={page}
          active={page === currentPage}
          onClick={() => handlePageChange(page)}
        >
          {page}
        </Pagination.Item>
      );
    }
    
    // Nút trang cuối
    if (currentPage < totalPages - 1) {
      if (currentPage < totalPages - 2) {
        items.push(<Pagination.Ellipsis key="ellipsis-end" />);
      }
      
      items.push(
        <Pagination.Item
          key={totalPages}
          onClick={() => handlePageChange(totalPages)}
        >
          {totalPages}
        </Pagination.Item>
      );
    }
    
    // Nút Next
    items.push(
      <Pagination.Next
        key="next"
        onClick={() => handlePageChange(Math.min(totalPages, currentPage + 1))}
        disabled={currentPage === totalPages}
      />
    );
    
    return <Pagination>{items}</Pagination>;
  };
  
  return (
    <div className="container my-4">
      <h2 className="text-center mb-4">Đơn Hàng Của Tôi</h2>
      
      {/* Phần bộ lọc */}
      <Row className="mb-3">
        <Col md={4}>
          <Form.Group>
            <Form.Label>Lọc theo trạng thái</Form.Label>
            <Form.Select
              value={filterStatus}
              onChange={handleStatusFilterChange}
            >
              <option value="">Tất cả trạng thái</option>
              <option value="pending">Chờ xác nhận</option>
              <option value="processing">Đang xử lý</option>
              <option value="shipped">Đang giao hàng</option>
              <option value="delivered">Đã giao hàng</option>
              <option value="cancelled">Đã hủy</option>
              <option value="paid">Đã thanh toán</option>
              <option value="failed">Thanh toán thất bại</option>
            </Form.Select>
          </Form.Group>
        </Col>
      </Row>
      
      {/* Hiển thị lỗi nếu có */}
      {error && (
        <div className="alert alert-danger" role="alert">
          {error}
        </div>
      )}
      
      {/* Hiển thị trạng thái đang tải */}
      {loading ? (
        <div className="text-center my-5">
          <Spinner animation="border" role="status">
            <span className="visually-hidden">Đang tải...</span>
          </Spinner>
          <p className="mt-2">Đang tải dữ liệu...</p>
        </div>
      ) : (
        <>
          {/* Hiển thị khi không có đơn hàng */}
          {orders.length === 0 ? (
            <div className="text-center my-5">
              <p>Bạn chưa có đơn hàng nào.</p>
              <Link to="/products" className="btn btn-primary">
                Tiếp tục mua sắm
              </Link>
            </div>
          ) : (
            <>
              {/* Bảng danh sách đơn hàng */}
              <Table responsive striped bordered hover className="my-3">
                <thead>
                  <tr>
                    <th>Mã đơn hàng</th>
                    <th>Ngày đặt</th>
                    <th>Tổng tiền</th>
                    <th>Trạng thái</th>
                    <th>Thao tác</th>
                  </tr>
                </thead>
                <tbody>
                  {orders.map((order) => (
                    <tr key={order._id}>
                      <td>{order._id}</td>
                      <td>{formatDate(order.createdAt)}</td>
                      <td>{formatPrice(order.totalAmount)}</td>
                      <td>
                        <Badge bg={orderStatusVariants[order.status] || 'secondary'}>
                          {orderStatusLabels[order.status] || order.status}
                        </Badge>
                      </td>
                      <td>
                        <Button
                          variant="info"
                          size="sm"
                          className="me-2"
                          onClick={() => handleViewOrderDetail(order._id)}
                        >
                          Xem chi tiết
                        </Button>
                        
                        {(order.status === 'pending' || order.status === 'processing') && (
                          <Button
                            variant="danger"
                            size="sm"
                            onClick={() => handleCancelOrder(order._id)}
                            disabled={cancellingOrderId === order._id}
                          >
                            {cancellingOrderId === order._id ? (
                              <>
                                <Spinner
                                  as="span"
                                  animation="border"
                                  size="sm"
                                  role="status"
                                  aria-hidden="true"
                                  className="me-1"
                                />
                                Đang hủy...
                              </>
                            ) : (
                              'Hủy đơn hàng'
                            )}
                          </Button>
                        )}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </Table>
              
              {/* Phần phân trang */}
              <div className="d-flex justify-content-center mt-4">
                {renderPagination()}
              </div>
            </>
          )}
        </>
      )}
    </div>
  );
};

export default MyOrders; 