import axios from 'axios';

// Đường dẫn API có thể cần điều chỉnh tùy theo cấu trúc backend
const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api/orders';

// Tạo axios instance với cấu hình chung
const axiosInstance = axios.create({
  baseURL: API_URL,
  withCredentials: true
});

// Thêm interceptor để tự động thêm token vào header
axiosInstance.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('token');
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => Promise.reject(error)
);

/**
 * Lấy danh sách đơn hàng của người dùng đã đăng nhập
 * @param {Object} options - Các tùy chọn lọc (status, page, limit)
 * @returns {Promise} Danh sách đơn hàng với phân trang
 */
export const getMyOrders = async (options = {}) => {
  try {
    // Tạo query parameters từ options
    const params = new URLSearchParams();
    if (options.status) params.append('status', options.status);
    if (options.page) params.append('page', options.page);
    if (options.limit) params.append('limit', options.limit);
    
    console.log(`Gọi API: ${API_URL}/?${params.toString()}`);
    const response = await axiosInstance.get(`/?${params.toString()}`);
    
    return {
      success: true,
      orders: response.data.orders || [],
      pagination: response.data.pagination || {
        total: 0,
        page: 1,
        limit: 10,
        totalPages: 0
      }
    };
  } catch (error) {
    console.error('Lỗi khi lấy danh sách đơn hàng:', error);
    return {
      success: false,
      message: error.response?.data?.message || 'Không thể kết nối đến máy chủ',
      orders: [],
      pagination: {
        total: 0,
        page: 1,
        limit: 10,
        totalPages: 0
      }
    };
  }
};

/**
 * Lấy chi tiết đơn hàng theo ID
 * @param {string} orderId - ID của đơn hàng
 * @returns {Promise} Chi tiết đơn hàng
 */
export const getOrderById = async (orderId) => {
  try {
    console.log(`Gọi API: ${API_URL}/${orderId}`);
    const response = await axiosInstance.get(`/${orderId}`);
    return {
      success: true,
      order: response.data.order || {}
    };
  } catch (error) {
    console.error(`Lỗi khi lấy chi tiết đơn hàng ID ${orderId}:`, error);
    return {
      success: false,
      message: error.response?.data?.message || 'Không thể kết nối đến máy chủ',
      order: {}
    };
  }
};

/**
 * Hủy đơn hàng
 * @param {string} orderId - ID của đơn hàng cần hủy
 * @returns {Promise} Kết quả hủy đơn hàng
 */
export const cancelOrder = async (orderId) => {
  try {
    console.log(`Gọi API: ${API_URL}/${orderId}/cancel`);
    const response = await axiosInstance.patch(`/${orderId}/cancel`);
    return {
      success: true,
      message: response.data.message || 'Đơn hàng đã được hủy thành công',
      order: response.data.order || {}
    };
  } catch (error) {
    console.error(`Lỗi khi hủy đơn hàng ID ${orderId}:`, error);
    return {
      success: false,
      message: error.response?.data?.message || 'Không thể kết nối đến máy chủ'
    };
  }
}; 